import React from "react";
import { Collapsible, CollapsibleItem } from "react-materialize";

class Currently extends React.Component {
  render() {
    return (
      <div className="currently">
        <h5>
          This page is a showcase of items I find interesting at the moment. Use
          it as a source of inspiration.
        </h5>
        <Collapsible accordion className="collapsible">
          <CollapsibleItem className="ci" header="In Other Words" icon="class">
            {" "}
            <a
              href="https://www.goodreads.com/book/show/25614298-in-other-words"
              target="_blank"
            >
              In Other Words
            </a>{" "}
            - the latest novel by Jhumpa Lahiri, the author of{" "}
            <a
              href="https://en.wikipedia.org/wiki/The_Namesake"
              target="_blank"
            >
              The Namesake
            </a>
            , who has moved to Italy and writes now entirely in Italian.
          </CollapsibleItem>
          <CollapsibleItem
            className="ci"
            header="Be The Cowboy - Mitski"
            icon="headset"
          >
            The latest album from Mitski,{" "}
            <a
              target="_blank"
              href="https://open.spotify.com/album/653wRjqO0GOZPQPcXpeAXD?si=LpSdUuBxRs6Nu08mwUeKPA"
            >
              Be The Cowboy
            </a>{" "}
            is the best music out there.
          </CollapsibleItem>
        </Collapsible>
      </div>
    );
  }
}

export default Currently;



